body {
  margin: 0;
  padding: 0;

  font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif; 
  font-weight: 300;
  font-size: 16px;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

th {
	text-align: left;
}

th, td {
  vertical-align: middle;  

  padding: 10px;
  border: 1px solid #252526;
  
}

table {
	border-collapse: collapse;
	width:calc(100% - 20px);	
}
.AccountsTable {
	
}

.right {
  text-align: right;
}

.InspectButton {	
    width:100%; 

    padding: 2px;
    text-align: center;
    
    display: inline-block;
  	font-size: 16px;
}

div.rt-tr {
  align-items: center;
}

.ReactTable {
  margin-top:20px;
  margin-left:20px;
  height: calc(100% - 60px); /* Full-height: remove this if you want "auto" height */
  width: calc(100% - 40px); /* Set the width of the sidebar */
}

.LiquidateButton {
	width:200px; 

	margin-left: calc(100% - 220px);

    padding: 10px;
    
  	font-size: 16px;
}
.Loading {
	display:block; 
    margin:0 auto;
    text-align:center;
    margin-top:100px;
}
